@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
html {
  height: 100%;
}

body {
  background-color: #FBE6D0;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;

}

#root {
  max-width: 750px;
  min-height: 100vh;
  width: 90%;

  display: flex;
  flex-direction: column;
  margin: auto;
}

.switch-wrapper {
  position: relative;
  flex: auto;
  overflow: auto;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.switch-wrapper > div {
  position: relative;
}

.content {
  font-family: 'Montserrat', sans-serif;
  color: black;
  width: 100%;
  margin: auto;
}

.icons {
  padding-top: 2rem;
  padding-top: 2rem;
  font-size: 2rem;
}

.icons a {
  color: #1B7466;
  transition: .4s;
  padding-right: 0.5rem;
}

.icons a:hover {
  color: #0a2925;
}

#embed {
  width: 100%;
}
/* reusable components e.g. buttons, titles, etc. */

.title {
  font-family: Comfortaa, sans-serif;
  color: #ea4111;
  font-size: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;

  background: linear-gradient(270deg, #1889A0, #b71ad4, #ea4111);
  background-size: 600% 600%;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: rainbowGradient 7s ease infinite;
  animation: rainbowGradient 7s ease infinite;
}

.title-no-color {
  font-family: 'Comfortaa', sans-serif;
  font-size: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.subtitle {
  font-family: 'Comfortaa', sans-serif;
  color: #1B7466;
  font-size: 1.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.body-text {
  font-family: 'Montserrat', DejaVu Sans, Verdana, sans-serif;
  color: black;
  font-size: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.body-text a {
  color: #1B7466;
  text-decoration: none;
  transition: .4s;
}

.body-text a:hover {
  text-decoration: underline;
}

@-webkit-keyframes rainbowGradient {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
@keyframes rainbowGradient {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}

.round_btn {
  font-family: 'Montserrat', DejaVu Sans, Verdana, sans-serif;
  font-size: 1rem;
  display: inline-block;
  padding: 0.5em 1em;
  border-radius: 25px;
  text-decoration: none;
  background-color: #1B7466;
  color: white;
  border: solid 2px #1B7466;
  transition: .4s;
  margin-top: 1rem;
  margin-bottom: 1rem;
  margin-right: 0.5rem;
}

.round_btn:hover {
  background: #0a2925;
  border: solid 2px #0a2925;
  color: white;
}

.round_btn:disabled {
  background: #4d4d4d;
  border: solid 2px #4d4d4d;
}

.icons {
  padding-top: 2rem;
  padding-bottom: 2rem;
  font-size: 2rem;
}

.icons a {
  color: #1B7466;
  transition: .4s;
  padding-right: 0.5rem;
}

.icons a:hover {
  color: #0a2925;
}
/* css and layout for header */

.header {
  font-family: 'Montserrat', DejaVu Sans, Verdana, sans-serif;
  overflow: auto;
  padding-top: 2rem;
  padding-bottom: 2rem;
  flex: none;
}

.header a {
  float: left;
  color: #1B7466;
  text-align: center;
  padding: 0.6rem;
  text-decoration: none;
  font-size: 1.5rem; 
  transition: .4s;
}

.header a.logo {
  font-size: 1.3rem;
  background: linear-gradient(270deg, #1889A0, #b71ad4, #ea4111);
  background-size: 600% 600%;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: rainbowGradient 7s ease infinite;
  animation: rainbowGradient 7s ease infinite;
}

@-webkit-keyframes rainbowGradient {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
@keyframes rainbowGradient {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}

.header a:hover {
  color: #0a2925;
}

.header-right {
  float: right;
}
